<template>
<div class="content-wrapper">
    <div class="container">
        <div class="login-box">
            <div class="login-box-body">
                <p class="login-box-msg">Đăng nhập vào tizi</p>
                <form v-on:submit="login" action="javascript:void(0)">
                    <div class="form-group has-feedback">
                        <input v-model="user.username" name="username" v-validate="'required'" type="text" class="form-control" placeholder="Tài khoản hoặc email" />
                        <span class="glyphicon glyphicon-envelope form-control-feedback"></span>
                        <span class="error">{{ errors.first("username") }}</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.password" name="password" v-validate="'required'" type="password" class="form-control" placeholder="Mật khẩu" />
                        <span class="glyphicon glyphicon-lock form-control-feedback"></span>
                        <span class="error">{{ errors.first("password") }}</span>
                    </div>
                    <div class="row">
                        <div class="col-xs-8">
                            <a href="#" class="btn btn-facebook" style="margin-right:5px;">
                                <i class="fa fa-facebook"></i>
                            </a>
                            <a href="#" class="btn btn-google">
                                <i class="fa fa-google-plus"></i>
                            </a>
                        </div>
                        <div class="col-xs-4">
                            <button type="submit" class="btn btn-primary btn-block btn-flat">Đăng nhập</button>
                        </div>
                    </div>
                </form>
                <router-link :to="{name: 'forgotpassword'}" >Quên mật khẩu</router-link>
                <br />
                <router-link :to="{name: 'register'}" class="text-center">Đăng ký tài khoản</router-link>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { Validator } from "vee-validate";
import axios from 'axios';

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            user:{
                username:'',
                password:''
            },
            dictionary: {
                vi: {
                    custom: {
                        "username":{
                            required: "Vui lòng nhập tài khoản hoăc email !"
                        },
                        "password":{
                            required: "Vui lòng nhập mật khẩu !"
                        }
                    }
                }
            }
        }
    },
    methods: {
        login(){
            this.$validator.validate().then(async valid => {
                if(valid){
                    const user = await axios.post(`${this.api}/auth/login`, this.user);
                    if(user.data.success===1){
                        localStorage.token = user.data.token;
                        this.$store.state.token = user.data.token;
                        this.$store.state.user = user.data.user;
                        this.flash(user.data.message, 'success', {
                            timeout: 3000
                        });
                        this.$store.commit('setDataAfterLogin', user.data.user.id);
                        var redirect = this.$store.state.route ? this.$store.state.route : {name:'home'};
                        
                        this.$router.push(redirect);
                    }else this.$swal(user.data.message,"", "error");
                }
            })
            .catch(error=>{
                console.log(error);
            });
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
    }
}
</script>

<style>
.error{
    display: block;
    color: red;
    margin-left: 1px;
}
.content-wrapper{
    min-height: 682px;
}
@media(max-width: 768px){
    .content-wrapper{
        min-height: 100%;
    }
}
</style>